.header {
  background-color: $white;
  position: relative;
  z-index: 20;

  &::before {
    position: absolute;
    top: 0;
    content: '';
    height: 41px;
    width: 100%;
    display: block;
    z-index: 21;
    background: #C4C4C4;
  }
}

.header__container {
  display: flex;
  padding: 33px 0;
  align-items: center;

  @media(max-width: 1023px) {
    padding: 20px 0 15px 0;
  }
}

.header__menu {
  @include list-reset;
  display: flex;

  & a {
    text-decoration: none;
    font-size: 18px;
    line-height: 145%;
    color: #27363A;
    padding: 2px 5px;
  }

  & a:hover {
    transition: all 0.4s ease;
    opacity:0.7;
  }

  @media(max-width: 1023px) {
    display: flex;
    flex-direction: column;
    background: #fff url(../img/menu-back.png) no-repeat bottom left;
    width: calc(100% - 22px);
    margin: 89px auto 0;
    align-items: center;
    justify-content: center;
    padding: 30px 27px;

    & li {
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      width: 100%;
    }

    & a {
      text-decoration: none;
      font-size: 18px;
      line-height: 145%;
      color: #373737;
      padding: 0;
      text-align: center;
      font-weight: 700;
      width: 100%;
    }
  }
}

.header__menu-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: transparent;
  z-index: 20;
  align-items: center;
  visibility: hidden;
}


.header__nav {
  margin-left: auto;
}

.header__button {
  text-decoration: none;
  font-size: 16px;
  line-height: 145%;
  text-align: center;
  position: relative;

  @media(max-width: 1023px) {
    font-size: 10px;
    line-height: 145%;
  }

  & svg {
    position: absolute;
    top: 50%;
    left:50%;
    transform: translateY(-50%) translateX(-50%);
    z-index: 2;

    @media(max-width: 1023px) {
      max-width: 130px;
    }
  }

  & span {
    position: relative;
    color: #92BD72;
    z-index: 3;
    padding: 0 55px;

    @media(max-width: 1023px) {
      padding: 0 30px;
    }
  }

  &:hover svg path {
    fill: #92BD72;
    transition: fill 0.4s ease;
  }

  &:hover span{
    color: $white;
    transition: color 0.4s ease;
  }
}

.header__burger {
  background-color: transparent;
  border: none;
  padding: 0;
  cursor: pointer;
  display: none;
  margin-right: 12px;

  @media(max-width: 1023px) {
    display: block;
  }
}

.header__close {
  position: fixed;
  top: 68px;
  right: 12px;
  z-index: 21;
  background-color: transparent;
  border: none;
  padding: 0;
  cursor: pointer;
  width: 25px;
      visibility: hidden;
    opacity: 0;

  & img {
    width: 100%;
  }
}

.header__menu_active {
  visibility: visible;
  opacity: 1;
  transition: all 0.4s ease;
}

.header__close_active {
  visibility: visible;
  opacity: 1;
  transition: all 0.4s ease;

  @media(min-width: 1024px) {
    visibility: hidden;
    opacity: 0;
  }
}
