.footer {
  min-height: 230px;
  display: flex;
  align-items: center;
  position: relative;
  z-index: 4;

  @media(max-width: 1400px) {
    padding: 30px 0;
  }
}

.footer__container {
  margin: 0 auto;
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 1760px;
  padding: 0 20px;

  @media(max-width: 1440px) {
    flex-wrap: wrap;
  }
}

.footer__logo-second {
  display: flex;
  margin-left: 50px;

  @media(max-width: 1400px) {
    margin-left: auto;
  }

  @media(max-width: 767px) {
    margin-left: 0;


   & img {
    max-width: 120px
  }
  }
}

.footer__logo-first {
  display: flex;
  align-items: center;
  margin-left: 220px;

  @media(max-width: 1700px) {
    margin-left: 100px;
  }

  @media(max-width: 1400px) {
    margin-left: 0px;
  }

  @media(max-width: 767px) {
   margin-bottom: 30px;

  }
}

.footer__logo-first span {
  margin-left: 16px;
  font-size: 16px;
  line-height: 16px;
  color: rgba(39, 54, 58, 0.66);
  max-width: 100px;
}

.footer__logo-second span {
  font-size: 16px;
  line-height: 16px;
  color: rgba(39, 54, 58, 0.66);
  margin-left: 12px;
  max-width: 50px;
}
